import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysDefineMappingControl } from '../entities/sys_define_mapping_control.entity';

@Injectable()
export class DefineMappingControlService {
  constructor(
    @InjectRepository(SysDefineMappingControl)
    private readonly defineMappingControlRepository: Repository<
      SysDefineMappingControl
    >
  ) {}

  async getDefineMappingControls(context: any) {
    return await this.defineMappingControlRepository.find({
      company: context.company,
      pid: context.pid
    });
  }

  async getDefineMappingControl(context: any) {
    return await this.defineMappingControlRepository.find({
      company: context.company,
      pid: context.pid,
      item_no: context.item_no
    });
  }
}
