import { Body, Controller, Post } from '@nestjs/common';

import { DefineSearchService } from '../services/define-search.service';

@Controller('define-search')
export class DefineSearchController {
  constructor(private readonly defineSearchService: DefineSearchService) {}

  @Post('get/many')
  getDefineSearch(@Body() body) {
    return this.defineSearchService.getDefineSearch(body);
  }
}
