import { Body, Controller, Post } from '@nestjs/common';

import { DefineMappingService } from '../services/define-mapping.service';

@Controller('define-mapping')
export class DefineMappingController {
  constructor(private readonly defineMappingService: DefineMappingService) {}

  @Post('get/many')
  getDefineMapping(@Body() body) {
    return this.defineMappingService.getDefineMapping(body);
  }
}
