import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysDefineMapping } from '../entities/sys_define_mapping.entity';

@Injectable()
export class DefineMappingService {
  constructor(
    @InjectRepository(SysDefineMapping)
    private readonly defineMappingRepository: Repository<SysDefineMapping>
  ) {}

  async getDefineMapping(context: any) {
    return await this.defineMappingRepository.find({
      company: context.company,
      pid: context.pid
    });
  }
}
