import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysDepartment } from '../entities/sys_department.entity';

@Injectable()
export class DepartmentService {
  constructor(
    @InjectRepository(SysDepartment)
    private readonly departmentRepository: Repository<SysDepartment>
  ) {}

  async getDepartments(context: any) {
    return await this.departmentRepository.find({
      company: context.company,
      lang: context.lang
    });
  }

  async getDepartment(context: any) {
    return await this.departmentRepository.find({
      company: context.company,
      lang: context.lang,
      code: context.code
    });
  }
}
