import { Body, Controller, Post } from '@nestjs/common';

import { DefineGridService } from '../services/define-grid.service';

@Controller('define-grid')
export class DefineGridController {
  constructor(private readonly defineGridService: DefineGridService) {}

  @Post('get/many')
  getDefineGrid(@Body() body) {
    return this.defineGridService.getDefineGrid(body);
  }
}
