import { Body, Controller, Post } from '@nestjs/common';

import { DefineInputService } from '../services/define-input.service';

@Controller('define-input')
export class DefineInputController {
  constructor(private readonly defineInputService: DefineInputService) {}

  @Post('get/many')
  getDefineInput(@Body() body) {
    return this.defineInputService.getDefineInput(body);
  }
}
