import { Body, Controller, Delete, Param, Post } from '@nestjs/common';

import { EmployeeService } from '../services/employee.service';

@Controller('employee')
export class EmployeeController {
  constructor(private readonly employeeService: EmployeeService) {}

  @Post('get/many')
  getEmployees(@Body() body) {
    return this.employeeService.getEmployees(body);
  }

  @Post('get/one')
  getEmployee(@Body() body) {
    return this.employeeService.getEmployee(body);
  }

  @Post('add')
  addEmployee(@Body() body) {
    return this.employeeService.addEmployee(body);
  }

  @Post('update')
  updateEmployee(@Body() body) {
    return this.employeeService.updateEmployee(body);
  }

  @Delete(':id')
  deleteEmployee(@Param() params) {
    return this.employeeService.deleteEmployee(params.id);
  }

  @Post('delete/many')
  deleteEmployees(@Body() body) {
    return this.employeeService.deleteEmployees(body);
  }
}
