import { Column, Entity, PrimaryColumn } from 'typeorm';

@Entity()
export class SysDefineGrid {
  @PrimaryColumn('character varying', { length: 20 })
  company: any;

  @PrimaryColumn('character varying', { length: 20 })
  pid: any;

  @PrimaryColumn('character varying', { length: 20 })
  grid_id: any;

  @PrimaryColumn('integer')
  item_no: any;

  @PrimaryColumn('integer')
  data_type: any;

  @Column('character varying', { nullable: true , length: 100 })
  data_format: any;

  @Column('integer')
  cap_type: any;
  
  @Column('character varying', { length: 20 })
  cap_code: any;

  @Column('integer')
  change_count: any;

  @Column('character varying', { length: 20 })
  create_emp_id: any;

  @Column('timestamp without time zone')
  create_datetime: any;

  @Column('character varying', { length: 20 })
  change_emp_id: any;

  @Column('timestamp without time zone')
  change_datetime: any;

  @Column('character')
  data_flag: any;
}
