import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysDefineDisplay } from '../entities/sys_define_display.entity';

@Injectable()
export class DefineDisplayService {
  constructor(
    @InjectRepository(SysDefineDisplay)
    private readonly defineDisplayRepository: Repository<SysDefineDisplay>
  ) {}

  async getDefineDisplays(context: any) {
    return await this.defineDisplayRepository.find({
      company: context.company,
      emp_id: context.emp_id,
      pid: context.pid,
      group_id: context.group_id
    });
  }

  async getDefineDisplay(context: any) {
    return await this.defineDisplayRepository.find({
      company: context.company,
      emp_id: context.emp_id,
      pid: context.pid,
      group_id: context.group_id,
      item_no: context.item_no
    });
  }
}
