import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysDefineMappingFunction } from '../entities/sys_define_mapping_function.entity';

@Injectable()
export class DefineMappingFunctionService {
  constructor(
    @InjectRepository(SysDefineMappingFunction)
    private readonly defineMappingFunctionRepository: Repository<
      SysDefineMappingFunction
    >
  ) {}

  async getDefineMappingFunction(context: any) {
    return await this.defineMappingFunctionRepository.find({
      company: context.company,
      pid: context.pid
    });
  }
}
