import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysDefineGrid } from '../entities/sys_define_grid.entity';

@Injectable()
export class DefineGridService {
  constructor(
    @InjectRepository(SysDefineGrid)
    private readonly defineGridRepository: Repository<SysDefineGrid>
  ) {}

  async getDefineGrid(context: any) {
    return await this.defineGridRepository.find({
      company: context.company,
      pid: context.pid,
      grid_id: context.grid_id
    });
  }
}
