import { Column, Entity, PrimaryColumn } from 'typeorm';

@Entity()
export class SysDefineMapping {
  @PrimaryColumn('character varying', { length: 20 })
  company: any;

  @PrimaryColumn('character varying', { length: 20 })
  pid: any;

  @PrimaryColumn('character varying', { length: 20 })
  mapping_group_id: any;

  @PrimaryColumn('integer')
  mapping_item_no: any;

  @PrimaryColumn('character varying', { length: 250 })
  mapping_item_field: any;

  @PrimaryColumn('character varying', { length: 50 })
  mapping_table_name: any;

  @PrimaryColumn('character varying', { length: 50 })
  mapping_column_name: any;

  @PrimaryColumn('integer')
  mapping_data_type: any;

  @Column('integer')
  change_count: any;

  @Column('character varying', { length: 20 })
  create_emp_id: any;

  @Column('timestamp without time zone')
  create_datetime: any;

  @Column('character varying', { length: 20 })
  change_emp_id: any;

  @Column('timestamp without time zone')
  change_datetime: any;

  @Column('character')
  data_flag: any;
}
