import { SysDefineClass } from '../entities/sys_define_class.entity';
import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

@Injectable()
export class DefineClassService {
  constructor(
    @InjectRepository(SysDefineClass)
    private readonly defineClassRepository: Repository<SysDefineClass>
  ) {}

  async getDefineClasses(context: any) {
    return await this.defineClassRepository.find({
      company: context.company,
      lang: context.lang
    });
  }

  async getDefineClass(context: any) {
    return await this.defineClassRepository.find({
      company: context.company,
      lang: context.lang,
      code: context.code
    });
  }
}
