import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysMaster } from '../entities/sys_master.entity';

@Injectable()
export class MasterService {
  constructor(
    @InjectRepository(SysMaster)
    private readonly masterRepository: Repository<SysMaster>
  ) {}

  async getMasters(context: any) {
    return await this.masterRepository.find({
      company: context.company,
      lang: context.lang,
      class_code: context.class_code
    });
  }

  async getMaster(context: any) {
    return await this.masterRepository.find({
      company: context.company,
      lang: context.lang,
      class_code: context.class_code,
      code: context.code
    });
  }
}
