import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysLang } from '../entities/sys_lang.entity';

@Injectable()
export class LangService {
  constructor(
    @InjectRepository(SysLang)
    private readonly langRepository: Repository<SysLang>
  ) {}

  async getLangs(context: any) {
    return this.langRepository.find({
      company: context.company
    });
  }

  async getLang(context: any) {
    return this.langRepository.find({
      company: context.company,
      code: context.code
    });
  }
}
