import { Column, Entity, PrimaryColumn } from 'typeorm';

@Entity()
export class SysPage {
  @PrimaryColumn('character varying', { length: 20 })
  company: any;

  @PrimaryColumn('character varying', { length: 20 })
  lang: any;

  @PrimaryColumn('character varying', { length: 20 })
  mid: any;

  @PrimaryColumn('character varying', { length: 20 })
  pid: any;

  @Column('character varying', { length: 100 })
  name: any;

  @Column('boolean')
  active_flag: any;

  @Column('character varying', { length: 1000 })
  description: any;

  @Column('character varying', { length: 250 })
  icon: any;

  @Column('character varying', { length: 20 })
  slug: any;

  @Column('integer')
  sort_no: any;

  @Column('character varying', { length: 50 })
  mapping_table_name: any;

  @Column('character varying', { length: 50 })
  mapping_entity_name: any;

  @Column('integer')
  change_count: any;

  @Column('character varying', { length: 20 })
  create_emp_id: any;

  @Column('timestamp without time zone')
  create_datetime: any;

  @Column('character varying', { length: 20 })
  change_emp_id: any;

  @Column('timestamp without time zone')
  change_datetime: any;

  @Column('character')
  data_flag: any;
}
