import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysDefineInput } from '../entities/sys_define_input.entity';

@Injectable()
export class DefineInputService {
  constructor(
    @InjectRepository(SysDefineInput)
    private readonly defineInputRepository: Repository<SysDefineInput>
  ) {}

  async getDefineInput(context: any) {
    return await this.defineInputRepository.find({
      company: context.company,
      pid: context.pid,
      input_id: context.input_id
    });
  }
}
