import { Column, Entity, PrimaryColumn } from 'typeorm';

@Entity()
export class SysDefineMappingFunction {
  @PrimaryColumn('character varying', { length: 20 })
  company: any;

  @PrimaryColumn('character varying', { length: 20 })
  pid: any;

  @PrimaryColumn('integer')
  screen_type: any;

  @PrimaryColumn('integer')
  function_type: any;

  @Column('boolean')
  active_flag: any;

  @Column('integer')
  change_count: any;

  @Column('character varying', { length: 20 })
  create_emp_id: any;

  @Column('timestamp without time zone')
  create_datetime: any;

  @Column('character varying', { length: 20 })
  change_emp_id: any;

  @Column('timestamp without time zone')
  change_datetime: any;

  @Column('character')
  data_flag: any;
}
