import { Column, Entity, PrimaryColumn } from 'typeorm';

@Entity()
export class SysDefineDisplay {
  @PrimaryColumn('character varying', { length: 20 })
  company: any;

  @PrimaryColumn('character varying', { length: 20 })
  emp_id: any;

  @PrimaryColumn('character varying', { length: 20 })
  pid: any;

  @PrimaryColumn('character varying', { length: 20 })
  group_id: any;

  @PrimaryColumn('integer')
  item_no: any;

  @Column('integer')
  disp_no: any;

  @Column('boolean')
  item_disp: any;

  @Column('integer')
  width: any;

  @Column('integer')
  height: any;

  @Column('integer')
  row_start: any;

  @Column('integer')
  row_end: any;

  @Column('integer')
  col_start: any;

  @Column('integer')
  col_end: any;

  @Column('text')
  color_bg: any;

  @Column('text')
  color_text: any;

  @Column('text')
  class: any;

  @Column('text')
  stype: any;

  @Column('integer')
  change_count: any;

  @Column('character varying', { length: 20 })
  create_emp_id: any;

  @Column('timestamp without time zone')
  create_datetime: any;

  @Column('character varying', { length: 20 })
  change_emp_id: any;

  @Column('timestamp without time zone')
  change_datetime: any;

  @Column('character')
  data_flag: any;
}
