import { Column, Entity, PrimaryColumn } from 'typeorm';

@Entity()
export class SysDefineMappingControl {
  @PrimaryColumn('character varying', { length: 20 })
  company: any;

  @PrimaryColumn('character varying', { length: 20 })
  pid: any;

  @PrimaryColumn('integer')
  item_no: any;

  @PrimaryColumn('character varying', { length: 20 })
  control_type: any;

  @PrimaryColumn('character varying', { length: 50 })
  mapping_table: any;

  @PrimaryColumn('character varying', { length: 50 })
  mapping_column0: any;

  @Column('character varying', { nullable: true, length: 50 })
  mapping_column1: any;

  @Column('character varying', { nullable: true, length: 50 })
  mapping_column2: any;

  @Column('character varying', { nullable: true, length: 50 })
  mapping_column3: any;

  @Column('character varying', { nullable: true, length: 50 })
  mapping_column4: any;

  @Column('character varying', { nullable: true, length: 50 })
  mapping_column5: any;

  @Column('character varying', { nullable: true, length: 50 })
  mapping_column6: any;

  @Column('character varying', { nullable: true, length: 50 })
  mapping_column7: any;

  @Column('character varying', { nullable: true, length: 50 })
  mapping_column8: any;

  @Column('character varying', { nullable: true, length: 50 })
  mapping_column9: any;

  @Column('integer')
  change_count: any;

  @Column('character varying', { length: 20 })
  create_emp_id: any;

  @Column('timestamp without time zone')
  create_datetime: any;

  @Column('character varying', { length: 20 })
  change_emp_id: any;

  @Column('timestamp without time zone')
  change_datetime: any;

  @Column('character')
  data_flag: any;
}
