import { Column, Entity, PrimaryColumn } from 'typeorm';

@Entity()
export class SysEmployee {
  @PrimaryColumn('character varying', { length: 20 })
  company: any;

  @PrimaryColumn('character varying', { length: 20 })
  lang: any;

  @PrimaryColumn('character varying', { length: 20 })
  code: any;

  @Column('character varying', { length: 100 })
  name: any;

  @Column('boolean')
  active_flag: any;

  @Column('character varying', { nullable: true , length: 250 })
  address1: any;

  @Column('character varying', { nullable: true , length: 250 })
  address2: any;

  @Column('character varying', { nullable: true , length: 50 })
  tel1: any;

  @Column('character varying', { nullable: true , length: 50 })
  tel2: any;

  @Column('character varying', { nullable: true , length: 50 })
  email: any;

  @Column('integer')
  sort_no: any;

  @Column('integer')
  change_count: any;

  @Column('character varying', { length: 20 })
  create_emp_id: any;

  @Column('timestamp without time zone')
  create_datetime: any;

  @Column('character varying', { length: 20 })
  change_emp_id: any;

  @Column('timestamp without time zone')
  change_datetime: any;

  @Column('character')
  data_flag: any;
}
