import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysCaption } from '../entities/sys_caption.entity';

@Injectable()
export class CaptionService {
  constructor(
    @InjectRepository(SysCaption)
    private readonly captionRepository: Repository<SysCaption>
  ) {}

  async getCaptions(context: any) {
    return await this.captionRepository.find({
      company: context.company,
      lang: context.lang
    });
  }

  async getCaption(context: any) {
    return await this.captionRepository.find({
      company: context.company,
      lang: context.lang,
      pid: context.pid,
      type: context.type,
      code: context.code
    });
  }
}
