import { Body, Controller, Post } from '@nestjs/common';

import { MasterDetailService } from '../services/master-detail.service';

@Controller('master-detail')
export class MasterDetailController {
  constructor(private readonly masterDetailService: MasterDetailService) {}

  @Post('get/many')
  getMasterDetails(@Body() body) {
    return this.masterDetailService.getMasterDetails(body);
  }

  @Post('get/one')
  getMasterDetail(@Body() body) {
    return this.masterDetailService.getMasterDetail(body);
  }
}
