import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysDefineGroup } from '../entities/sys_define_group.entity';

@Injectable()
export class DefineGroupService {
  constructor(
    @InjectRepository(SysDefineGroup)
    private readonly defineGroupRepository: Repository<SysDefineGroup>
  ) {}

  async getDefineGroups(context: any) {
    return await this.defineGroupRepository.find({
      company: context.company,
      pid: context.pid
    });
  }

  async getDefineGroup(context: any) {
    return await this.defineGroupRepository.find({
      company: context.company,
      pid: context.pid,
      group_id: context.group_id
    });
  }
}
