import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysDefineInputGrid } from '../entities/sys_define_input_grid.entity';

@Injectable()
export class DefineInputGridService {
  constructor(
    @InjectRepository(SysDefineInputGrid)
    private readonly defineInputGridRepository: Repository<SysDefineInputGrid>
  ) {}

  async getDefineInputGrid(context: any) {
    return await this.defineInputGridRepository.find({
      company: context.company,
      pid: context.pid,
      input_grid_id: context.input_grid_id
    });
  }
}
