import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysDefineSearch } from '../entities/sys_define_search.entity';

@Injectable()
export class DefineSearchService {
  constructor(
    @InjectRepository(SysDefineSearch)
    private readonly defineSearchRepository: Repository<SysDefineSearch>
  ) {}

  async getDefineSearch(context: any) {
    return await this.defineSearchRepository.find({
      company: context.company,
      pid: context.pid,
      search_id: context.search_id
    });
  }
}
