import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

import { SysMasterDetail } from '../entities/sys_master_detail.entity';

@Injectable()
export class MasterDetailService {
  constructor(
    @InjectRepository(SysMasterDetail)
    private readonly masterDetailRepository: Repository<SysMasterDetail>
  ) {}

  async getMasterDetails(context: any) {
    return await this.masterDetailRepository.find({
      company: context.company,
      lang: context.lang,
      class_code: context.class_code,
      master_code: context.master_code
    });
  }

  async getMasterDetail(context: any) {
    return await this.masterDetailRepository.find({
      company: context.company,
      lang: context.lang,
      class_code: context.class_code,
      master_code: context.master_code,
      code: context.code
    });
  }
}
